.table {
    background-color: #f2f0f0;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 30rem;
    padding: 2rem;
}
.table__heading {
    color: rgb(43, 102, 240);
    font-size: 2.5rem;
    text-transform: uppercase;
    text-align: center;
}